.movie{
    display: flex;
    border: 1px solid lightgrey;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
}

.img{
    max-width: 100%;
    height: auto;
    padding: 10px;
    display: flex;
}

.title{
    align-self: center;
}

.title a {
    color: lightgreen;
}

.title a:hover{
    color: white;
}

.title a:active{
    font-style: italic;
}

.rating{
    margin: 0;
    display: flex;
    align-self: flex-start;
    padding: 15px;
}

.summary{
    display: flex;
    padding: 20px;
    font-size: 22px;
}

.genre{
    margin: 0;
    display: flex;
    align-self: flex-start;
    padding: 10px;
}

