.container{
    display: flex;
    height: 100%;
    justify-content: center;
}

.header{
    text-align: center;
    color: lightgreen;
}


.movies {
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(1, minmax(300px, 1fr));
    grid-gap: 100px;
    width: 33%;
    padding-top: 50px;
  }

  @media screen and (max-width: 1090px) {
    .movies {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
  